export default {
  numberAnimation: (from, to, id) => {
    function increment() {
      setTimeout(function() {
        if ((to - from) === 0) {
          return false;
        }
        if (100 < to - from) {
          from += 100;
        }
        if (10 < to - from) {
          from += 10;
        }
        if (1 < to - from) {
          from++;
        }

        if (from <= to) {
          document.getElementById(id).innerHTML = from;
          increment();
        }
      }, 10);
    }
    increment();

  }
}
