import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

//const
import CONST from './common/const';

// content
import Home from './routes/Home';
import Expearience from './routes/Expearience';
import NotFound from './routes/NotFound';

// common
import Header from './components/Header';
class App extends Component {

  _getPath = (menu) => {
    const PATH = {...CONST.APP.PATH};
    return PATH['root'] + PATH[menu];
  }

  render() {
    
    return (
      <Router>
        <div>
          {/* <Header/> */}
          <div>
            <Switch>
              <Route exact path={this._getPath('Home')} component={Home} />
              <Route path={this._getPath('Expearience')} component={Expearience} />
              <Route component={NotFound} />
            </Switch>
          </div>
        </div>
      </Router>
    );
  }
}

export default App;
